.history--container{
    width: 100vw;
    height: 100vh;
    margin: 0 auto;
    background-color: #000;
}
.history--links__container{
    display: flex;
    justify-content: space-evenly;
    align-items: flex-end;
    margin-top: auto;
    width: 50%;
    margin: auto auto 0 auto;
}
.history--links__container > a {
    margin: 0 auto;
    font-size: 2.5rem;
    font-family: 'League Gothic', sans-serif;
}
.corazon{
    height: 800px;
    width: 100%;
    padding: 100px 100px 100px 0;
}
.dragon{
    height: 800px;
    width: 100%;
    padding: 100px 100px 100px 0;
}
.mort{
    height: 800px;
    width: 100%;
    padding: 100px 100px 100px 0;
}
.nexo{
    height: 800px;
    width: 100%;
    padding: 100px 100px 100px 0;
}
.history--text__container > h1{
    margin: 0 auto;
    font-family: 'League Gothic', sans-serif;
    width: 50%;
    font-size: 4rem;
}
.history--text__container > p{
    margin: 0 auto;
    width: 50%;
    font-size: 1.1rem;
    line-height: 30px;
    text-align: start;
}
.history--text__container{
    width: 100%;    
    margin: 0 auto;
    color: #fff;
    background-color: #000;
    display: flex;
    flex-direction: column;
    padding: 20px;
    text-align: center;
}
.poe--container{
    text-align: center;
}
.reference--button{
    width: 50%;
    border: none;
    background: transparent;
    color: #3992ff;
    cursor: pointer;
    font-size: 2rem;
    text-decoration: underline;
    display: flex;
    justify-content: center;
    margin: 20px auto 10px auto;
}
.history--logo{
    width:  60%;
    height: 60%;
    margin:  0 auto;
    border: 3px solid #fff;
    margin-bottom: 100px;
}