.home--container{
    z-index: 2;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 150px;
}
.home--welcome__container{
    text-align: center;
}
.home--welcome__container > h1{
    font-size: 5rem;
    font-family: 'League Gothic', sans-serif;
    font-weight: bold;
}
.home--welcome__container > p{
    font-size: 2.5rem;
}
.home--image__container{
    z-index: 2;
    display: flex;
    justify-content: center;
}
.home--image__container > img{
    width: 50%;
    height: 50%;
    border: 2px solid #fff;
    border-radius: 25px;
    align-self: center;
}
.home--title{
    margin: 150px 0;
    font-size: 6rem;
    font-family: 'League Gothic', sans-serif;
}
.home--histories__container{
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.home--history__container{
    text-align: center;
    width: 100%;
    max-height: 580px;
    border: 1px solid #000;
    border-radius: 25px 25px 0 0;
    cursor: pointer;
    margin: 30px;
    font-size: 1.5rem;
}
.first{
    box-shadow: 10px 10px 163px 51px rgba(152, 0, 0,0.75);
    -webkit-box-shadow: 10px 10px 163px 51px rgba(152, 0, 0,0.75);
    -moz-box-shadow: 10px 10px 163px 51px rgba(152, 0, 0,0.75);
    border: 1px solid #ff0000;
    color: #ff0000;
    margin-left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;    
}
.first > .home--history__logo{
    background-image: url('https://cdn.discordapp.com/attachments/551469906844188682/1040443272566423652/roja.png');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    height: 400px;
    width: 100%;
}
.second{
    box-shadow: 10px 10px 163px 51px rgba(255,255,255,0.75);
    -webkit-box-shadow: 10px 10px 163px 51px rgba(255,255,255,0.75);
    -moz-box-shadow: 10px 10px 163px 51px rgba(255,255,255,0.75);
    color: rgb(255,255,255);
    border: 1px solid #ffffffbf;
    margin-left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column; 
}
.second > .home--history__logo{
    background-image: url('https://cdn.discordapp.com/attachments/551469906844188682/1040443272084082748/blanca.png');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    height: 400px;
    width: 100%;
}
.third{
    box-shadow: 10px 10px 163px 51px rgba(9, 48, 154,0.75);
    -webkit-box-shadow: 10px 10px 163px 51px rgba(9, 48, 154,0.75);
    -moz-box-shadow: 10px 10px 163px 51px rgba(9, 48, 154,0.75);
    border: 1px solid #00a6ff;
    color: #00a6ff;
    margin-left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column; 
}
.third > .home--history__logo{
    background-image: url('https://cdn.discordapp.com/attachments/922917249919098971/1040827568045182996/azul.png');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    height: 400px;
    width: 100%;
}
.fourth{
    box-shadow: 10px 10px 163px 51px #ffb302 ;
    -webkit-box-shadow: 10px 10px 163px 51px #ffb302 ;
    -moz-box-shadow: 10px 10px 163px 51px #ffb302;
    border: #ffb302 2px solid;
    border-radius: 0%;
    width: 30%;
    position: relative;
    color: #ffb302;
}
.fourth > .home--history__logo{
    background-image: url('https://cdn.discordapp.com/attachments/922917249919098971/1039389002710650901/kp.png');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    height: 400px;
    width: 100%;
}
.home--link{
    text-align: center;
    width: 100%;
    height: 400px;
    border-radius: 25px;
    cursor: pointer;
    margin: 0 30px 50px 30px;
    text-decoration: none;
}
.home--history__text > h1{
    font-family: 'League Gothic', sans-serif;
    letter-spacing: 1px;
    font-weight: bolder;
    text-decoration: underline;
}
.history--text_a{
    color: #008cff;
    cursor: pointer;
}