.header{
    width: 100vw;
    height: 100px;
    border-bottom: 3px solid #fff;
    z-index: 2;
    display: flex;
    background-color: black;
}
.header--title{
    color: #fff;
    margin: auto 20px;
    font-size: 4rem;
    letter-spacing: 3px;
    font-family: 'League Gothic', sans-serif;
    font-weight: bold;
}
.header--li{
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin: auto 70px auto auto;
    font-family: 'League Gothic', sans-serif;
}
.header--ul{
    font-size: 2rem;
}
.header--ul > a{
    text-decoration: none;
    color: #fff;
}